/* App.css */
body, html, #root {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  background: #87ceeb; /* light blue sky color */
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;

.app-wrapper {
    min-height: 100vh;
    width: 100%;
    background-size: cover !important;
    background-position: center !important;
}
}

.overlay {
    background: rgba(0, 0, 0, 0.5); /* Dark semi-transparent box */
    padding: 1rem;
    border-radius: 12px;
    max-width: 400px;
    margin: auto; /* move the box down */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
    position: relative; /* Allows positioning */
    top: 2rem; /* Moves it down */
    z-index: 1; /* Ensures it appears above other content */

}

img {
  width: 100px;
  height: 100px;
}

h1, h2, p {
  margin: 0.5rem 0;
}
